import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import styled from 'styled-components'
import peach from '../images/glossypeach.gif'
import NameText from '../components/nametext'


const IndexPage = () => {

  const Container = styled.div`
  height: 70vh;
  font-family: Monospace;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  `
  const TextContainer = styled.div``

  const Name = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 0px;
  font-weight: 700;
  font-size: 3em;
  @media (max-width: 820px) {
    font-size: 2em;
  }
  `
  const IconContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: -20px;
  `

  const ImageContainer = styled.div`
  transition: transform 300ms ease-in-out;
  `
  const Trigger = styled.div`
  width: 100px;
  height: 100px; 
  margin: 0;
  &:hover ${ImageContainer} {
    transform: translate(100px, -50px) rotate(-20deg);
  }
`

  // var name = "Selambanchi Gessese";
  var first = ['S', 'e', 'l', 'a', 'm'];
  var last = ['G', 'e', 's', 's', 'e', 's', 'e'];
  const fancyFirst = first.map((letter, index) => {
    return <NameText letter={letter} id={index} />
  })
  const fancyLast = last.map((letter, index) => {
    return <NameText letter={letter} id={index} />
  })

  return (
    <Layout>
      <SEO title="Gessese" lang="en" description="Portfolio" />
      <Container>
        <TextContainer>
          <IconContainer>
            <Trigger>
              <ImageContainer>
                <img src={peach} alt="peachy"></img>
              </ImageContainer>
            </Trigger>
          </IconContainer>
          <Name>{fancyFirst}  {fancyLast}</Name>
          <p>Software Engineer</p>
        </TextContainer>
      </Container>

    </Layout>
  )
}

export default IndexPage
